const refAudioNotification = "data:audio/ogg;base64,"
var refAudio = new Audio();
refAudio.src = refAudioNotification;
refAudio.volume = 0.5;

const audioNotification = "data:audio/ogg;base64,"
var audio = new Audio();
audio.src = audioNotification;
audio.volume = 0.5;

import Component from 'flarum/Component';
import LoadingIndicator from 'flarum/components/LoadingIndicator';
import ChatMessage from './ChatMessage';
import ChatPreview from './ChatPreview';

export default class ChatViewport extends Component
{
    config(isInitialized, context)
    {
        if(!isInitialized)
        {
			let input = this.getChatInput();
			if(input)
			{
				input.baseScrollHeight = input.scrollHeight;
				input.baseHeight = 19;
			}
        }
    }

	init()
	{
        this.loading = false;
        this.scroll = {autoScroll: true, oldScroll: 0, loadingFetch: false, needToScroll: true};
		this.input = {messageLength: 0, rows: 1, preview: {}};
		this.messages = {components: [], instances: {}};

        this.permissions = {moderate: {}};
        if(!app.session.user) this.input.placeholder = app.translator.trans('pushedx-chat.forum.errors.unauthenticated');
        else
        {
            this.permissions.post = app.forum.attribute('pushedx-chat.permissions.chat');
            this.permissions.edit = app.forum.attribute('pushedx-chat.permissions.edit');
            this.permissions.delete = app.forum.attribute('pushedx-chat.permissions.delete');
            this.permissions.moderate.delete = app.forum.attribute('pushedx-chat.permissions.moderate.delete');
            this.permissions.moderate.vision = app.forum.attribute('pushedx-chat.permissions.moderate.vision');
            this.input.placeholder = app.translator.trans(this.permissions.post ? 'pushedx-chat.forum.chat.placeholder' : 'pushedx-chat.forum.errors.chatdenied')
        }

		let charLimit = app.forum.attribute('pushedx-chat.settings.charlimit');
		this.messageCharLimit = charLimit === null ? 512 : charLimit;

		this.chatFrame = this.props.chatFrame;
		this.chatPreview = this.props.chatPreview;
		this.model = this.props.model;
		this.messagesStorage = this.props.messagesStorage;
	}

	view()
	{
		if(!this.model) return <div><div className='wrapper' style={{height: this.chatFrame.transform.y + 'px'}}></div></div>

		return (
			<div>
				<div className='wrapper' 
					config={this.configScroll.bind(this)} 
					onscroll={this.disableAutoScroll.bind(this)}
					style={{height: this.chatFrame.transform.y + 'px'}}
				>
					{this.scroll.loadingFetch ?
						<div className='message-wrapper'>
							<LoadingIndicator className='loading-old Button-icon' />
						</div>
						: null
					}
					{this.getStoragedMessages().concat(this.input.writing ? this.input.preview.component : null)}
				</div>
				<div className='input-wrapper'>
					<textarea
						type = 'text'
						id = 'chat-input'
						maxlength = {this.messageCharLimit}
						disabled = {!this.permissions.post}
						placeholder = {this.input.placeholder}
						onkeypress = {this.inputPressEnter.bind(this)}
						oninput = {this.inputProcess.bind(this)}
						onpaste = {this.inputProcess.bind(this)}

						rows = {this.input.rows}
					/>
					{this.messageEditing ?
						<div className='icon edit' onclick={this.messageEditEnd.bind(this)}>
							<i class="fas fa-times"></i>
						</div>    
						: null
					}
					<div className='icon send' onclick={this.inputPressButton.bind(this)}>
						<i class="fas fa-angle-double-right"></i>
					</div>
					<div id='chat-limitter' 
						className={this.reachedLimit() ? 'reaching-limit' : ''}
						style={{display: !this.permissions.post ? 'none' : ''}}
					>
						{(this.messageCharLimit - (this.input.messageLength || 0)) + '/' + this.messageCharLimit}
					</div>
				</div>
			</div>
		)
	}

	getStoragedMessages()
	{
		return this.chatFrame.messagesStorage;
	}

    getChat()
    {
        return document.querySelector('.chat');
    }

    getChatHeader()
    {
        return document.querySelector('.chat #chat-header');
    }
    
    getchatFrame()
    {
        return document.querySelector('.chat .wrapper');
    }

    getChatInput()
    {
        return document.querySelector('.chat #chat-input');
    }

    getChatsList()
    {
        return document.querySelector('.chat #chats-list');
	}

    reachedLimit()
    {
        this.oldReached = (this.messageCharLimit - (this.input.messageLength || 0)) < 100;
        return this.oldReached;
    }

    configScroll(e)
    {
        if(this.scroll.oldScroll >= 0) e.scrollTop = this.scroll.oldScroll;
        else e.scrollTop = e.scrollHeight + this.scroll.oldScroll - 30;
    }

    disableAutoScroll(e)
    {
        let el = e.target;
        this.scroll.autoScroll = (el.scrollTop + el.offsetHeight >= el.scrollHeight);
        let currentHeight = el.scrollHeight;
        
        if(this.scroll.autoScroll) this.scroll.needToScroll = false;
        if(this.scroll.needToScroll) this.scrollToBottom();

        if(el.scrollTop <= 0 && this.scroll.oldScroll > 0 && !this.scroll.loadingFetch && !this.messageEditing) 
        {
            this.scroll.loadingFetch = true;
            this.scroll.oldScroll = -currentHeight;
            m.redraw();

           //this.apiFetch(Object.values(this.messages.instances)[0].id);
        }
        else 
        {
            m.redraw.strategy('none');
            this.scroll.oldScroll = el.scrollTop;
        }
	}
	
    chatOnResize()
    {
        if(this.scroll.autoScroll) 
            this.scrollToBottom();
    }

    scrollToBottom()
    {
        let chatFrame = this.getchatFrame();
        if(chatFrame)
        {
            if(this.scroll.timeout) clearTimeout(this.scroll.timeout);
            this.scroll.timeout = setTimeout(() => chatFrame.scroll({top: chatFrame.scrollHeight, behavior: 'smooth'}), 100);
            if(!this.scroll.autoScroll) this.scroll.needToScroll = true;
        }
	}

	inputClear()
	{
		input.value = '';
	}

	inputSyncWithPreview()
	{
		let input = this.getChatInput();
		if(this.input.writing)
			input.value = this.input.preview.instance.message;
	}
	
    inputProcess(e)
    {
        let input = this.getChatInput();
        this.input.messageLength = input.value.length;

        input.rows = 1;
        let rows = Math.ceil((input.scrollHeight - input.baseScrollHeight) / input.baseHeight) + 1;
        this.input.rows = rows;
        input.rows = rows;

        if(this.input.messageLength)
        {
            if(!this.input.writing && !this.messageEditing)
                this.inputPreviewStart();
        }
        else
        {
            if(this.input.writing && !input.value.length)
                this.inputPreviewEnd();
        }

        if(this.messageEditing) 
        {
            this.messageEditing.textFormatted = input.value;
            this.messageEditing.textFormat(input.value);
        }
        else if(this.input.writing)
        {
            let preview = this.input.preview.instance;
            preview.message = input.value;
            preview.textFormat(input.value);
        }
        this.timedRedraw(100, () => this.scroll.autoScroll && !this.messageEditing ? this.scrollToBottom() : null);
    }

    inputPressEnter(e)
    {
        if(this.loading) return false;
        if(e.keyCode == 13 && !e.shiftKey)
        {
            this.messageSend(this.getChatInput().value);
            return false;
        }
        else m.redraw.strategy('none');
        return true;
    }

    inputPressButton() 
    {
        this.messageSend(this.getChatInput().value);
    }

    inputClear()
    {
        this.input.messageLength = 0;
        this.input.rows = 1;
        this.getChatInput().value = '';
    }

    inputPreviewStart()
    {
        this.input.writing = true;

        if(!this.input.preview.component) 
        {
            this.input.preview.component = this.createMessage(
            {
                id: 0, 
                user_id: app.session.user.id(), 
                is_editing: true,
                message: ' '
            },
            {
                instanceGetter: (instance) => this.input.preview.instance = instance
            });
        }
        m.redraw();
    }

    inputPreviewEnd()
    {
        this.input.writing = false;

        m.redraw();
    }

    messageSend(text)
    {
        if(text.trim().length > 0 && !this.loading)
        {
            if(this.input.writing) 
            {
                this.input.writing = false;
    
                let message = this.input.preview.instance;
                message.created_at = new Date();
                message.is_editing = false;
    
                this.messages.components.push(this.input.preview.component);
                this.input.preview.component = null;
    
                m.redraw();
                message.flash();
            }

            this.inputClear();

            let editingMsg = this.messageEditing;
            if(editingMsg)
            {
                if(editingMsg.message.trim() !== editingMsg.textFormatted.trim()) editingMsg.edit(editingMsg.textFormatted);
                this.messageEditEnd();
            }
            else this.apiPost(text);
        }
    }

    messageEdit(message)
    {
        let chatInput = this.getChatInput();
        this.inputPreviewEnd();
        
        message.is_editing = true;
        message.textFormatted = message.message;

        this.messageEditing = message;
        chatInput.value = message.message;
        chatInput.focus();
        this.inputProcess();

        m.redraw();
    }

    messageEditEnd()
    {
        let message = this.messageEditing;

        message.is_editing = false;
        this.inputClear();
        message.textFormat();
        m.redraw();

        this.messageEditing = null;
    }

    messageDelete(data)
    {
        let message = this.messages.instances[data.id];
        if(message) 
        {
            if(this.permissions.moderate.vision) 
            {
                message.hide(data.deleted_by);
                m.redraw();
            }
            else message.elementWrapper.style.display = 'none'; 
        }
    }

    messageRestore(data)
    {
        let message = this.messages.instances[data.id];
        if(message) 
        {
            message.elementWrapper.style.display = ''; 
            message.deleted_by = null;
            m.redraw();
        }
        else
        {
            let messageIds = Object.keys(this.messages.instances);
            messageIds.some((value, index, array) => 
            {
                if(array[index - 1] < data.id && data.id < array[index])
                    return this.messages.components.splice(index, 0, this.createMessage(data));
            })
            m.redraw();
        }
    }

    messageResend(instance)
    {
		this.apiPost(instance.message, this).then(
			(result) =>
			{
				if(result)
				{
                    this.messages.components.some((value, index, array) => 
                    {
                        if(value == instance.component)
                            return array.splice(index, 1) && array.push(instance.component) && this.scrollToBottom();
                    });
                    instance.id = result.data.id;
                    instance.created_at = new Date();
                    instance.timedOut = false;
                    instance.needToFlash = true;

                    m.redraw();
				}
			}
		);
	}

	messagesUnload()
	{
		Object.values(this.messages.instances).map(c => c.elementWrapper ? c.elementWrapper.style.display = 'none' : null);
		this.inputClear();
	}

	messagesLoad()
	{
		if(this.messages.components.length) Object.values(this.messages.instances).map(c => c.elementWrapper ? c.elementWrapper.style.display = 'block' : null);
		else this.apiFetchChatMessages();
		this.inputSyncWithPreview();
	}

	apiFetchChatMessages()
	{
		let self = this;
		
		self.scroll.loadingFetch = true;
		m.redraw();

        app.request({
            method: 'GET',
            url: app.forum.attribute('apiUrl') + '/chats/' + this.model.id()
        }).then(
            function(response)
            {
                self.scroll.loadingFetch = false;
                self.scroll.autoScroll = false;

                let fetchedMessages = response.data.map((data) => self.createMessage(data.attributes));
				self.messages.components = fetchedMessages.concat(self.messages.components);
				
				self.chatFrame.messagesStorage = self.chatFrame.messagesStorage.concat(fetchedMessages);

                m.redraw();
            },
            function()
            {
                self.scroll.loadingFetch = false;
                m.redraw();
            }
        );
	}

    apiPost(text, targetInstance = this.input.preview.instance)
    {
        this.loading = true;
        this.scroll.needToScroll = true;

        return app.request({
            method: 'POST',
            url: app.forum.attribute('apiUrl') + '/chat',
            data: {msg: text}
        }).then(
            (result) =>
            {
                targetInstance.id = result.data.id;

                this.messages.instances[targetInstance.id] = targetInstance;
                if(targetInstance == this.input.preview.instance) this.input.preview.instance = null;
            
                this.loading = false;
                m.redraw();

                return result;
            },
            (error) =>
            {
                targetInstance.timedOut = true;
                if(targetInstance == this.input.preview.instance) this.input.preview.instance = null;

                this.loading = false;
                m.redraw();
            }
        );
	}
	
	timedRedraw(timeout, callback)
	{
		m.redraw.strategy('none');
		
        if(!this.redrawTimeout)
        {
		    this.redrawTimeout = setTimeout(() => {
                m.redraw();
                callback();
                this.redrawTimeout = null;
            }, timeout);
        }
	}

    createMessage(message, options = {}, notify = false) 
    {
        let chatMessage = new ChatMessage(
            Object.assign(message, 
            {
                actor: message.user_id,
                created_at: message.created_at || new Date(),
				chatViewport: this,
				chatFrame: this.chatFrame,
                instanceGetter: (instance) => this.messages.instances[instance.id] = instance,
                userResolved: this.messageUserResolved.bind(this, notify),
            }, options)
        );
        let component = m.component(chatMessage);
        chatMessage.component = component;
        return component;

        /* Working with lists prepend and flarum's component wrapper is complete anomaly
        If new components are added to the top of the list, new components get incorrect attributes (attrs of already existing components) when they are updated
        
        return (
            <ChatMessage 
                id={message.id} 
                message={message.message} 
                actor={message.user_id} 
                created_at={message.created_at} 
                chatFrame={this}
                instanceGetter={((instance) => this.messages.instances[instance.id] = instance)}
                userResolved={this.messageUserResolved.bind(this, notify)}
            />
        )
        */
    }

    messageUserResolved(notify, message)
    {
        if(notify && (!app.session.user || (message.user && message.user.id() != app.session.user.id()))) 
            this.notifyTry(message.message, message.user);

        if(this.scroll.needToScroll || this.scroll.autoScroll)
            this.scrollToBottom();

        if(notify) message.flash();
	}
	
    insertMention(message)
    {
        let user = message.user;
        if(!app.session.user) return;

        var input = this.getChatInput();
        input.value = input.value + " @" + user.username() + " ";
		input.focus();
		
		this.inputProcess();

        m.redraw.strategy('none');
    }

    messageIsMention(msg)
    {
        return app.session.user && (msg.indexOf('@' + app.session.user.username()) >= 0);
    }

    notifyTry(msg, user) 
    {
        if(!("Notification" in window)) return;
        if(this.notify && this.messageIsMention(msg)) this.notifySend(msg, user.username(), user.avatarUrl())
        this.notifySound(msg);
    }

    notifySend(msg, title, icon)
    {
        return !this.active ? new Notification(title, {body: msg, icon: icon, silent: true}) : null;
    }

    notifySound(msg) 
    {
        if(!this.isMuted) 
        {
            let sound = this.messageIsMention(msg) ? refAudio : audio;
            sound.currentTime = 0;
            sound.play();
        }
    }
}